.navBarMainContainer {
  height: 100px;
  width: 100%;
  border-bottom: 1px grey solid;
}

/* STANDARD NAV BAR*/

.standardNavBar {
  display: flex;
  justify-content: space-between;
}

.mobileNavBarMenu {
  display: none;
}

.navBarLogo {
  display: flex;
  align-items: center;
  padding: .5rem;
}

.navBarLogo img {
  max-height: 90px;
}

.navBarMainLinks {
  display: flex;
  align-items: center;
  padding: .5rem;
}

.navBarMainLinks a {
  padding: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
  font-family: 'Baloo Chettan 2', cursive;
}

.navBarSideLinks {
  display: flex;
  align-items: center;
  padding: .5rem;
  font-family: 'Baloo Chettan 2', cursive;
}

.navBarSideLinks a {
  padding: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
}

/* MOBILE NAV BAR*/

@media screen and (max-width: 840px) {
  .standardNavBar {
    display: none;
  }
  .mobileNavBarLogo img {
    height: 74px;
    padding: 2px;
  }
  .mobileNavBarBars svg {
    height: 40px;
    width: 40px;
    padding: 2px;
  }
  .mobileNavBarBars {
    display: flex;
    align-items: center;
  }
  .mobileNavBarMenu {
    display: flex;
    justify-content: space-between;
  }
  .navBarMainContainer {
    height: 75px;
    width: 100%;
    border-bottom: 1px grey solid;
  }
  .mobileNavBarMainLinks {
    position: absolute;
    top: 75px;
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 10, .85);
    width: 100%;
    align-items: flex-start;
    padding: 5px;
    font-family: 'Baloo Chettan 2', cursive;
  }
  .mobileNavBarMainLinks a {
    padding: 2px;
  }
}