.toolsMainContainer {
  display: flex;
  justify-content: center;
}

.toolsSubContainer {
  display: flex;
  flex-direction: row;
  margin: .5rem;
  padding: .5rem;
  max-width: 1000px
}

.toolsInformation {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.toolsImage img {
  width: 450px;
}

.toolsInfoTitle {
  font-size: 1.5rem;
}

.toolsInfoRegText {
  font-size: 1.15rem;
}

@media screen and (max-width: 840px) {
  .toolsSubContainer {
    flex-direction: column-reverse;
  }
}

@media screen and (max-width: 550px) {
  .toolsImage img {
    width: 325px;
  }
}