.footerMainContainer {
  height: 150px;
  background-color: #F1F1F1;
  /* border-top: 2px #444 solid; */
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.footerOfficeInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footerOfficeTitle {
  font-family: 'Baloo Chettan 2', cursive;
  font-size: 1.25rem
}

.footerContactInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footerContactTitle {
  font-family: 'Baloo Chettan 2', cursive;
  font-size: 1.25rem
}

.footerDetailsInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footerDetailsTitle {
  font-family: 'Baloo Chettan 2', cursive;
  font-size: 1.25rem
}

.footerSocialMedia {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media screen and (max-width: 840px) {
  .footerMainContainer {
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 2rem 2rem 2rem 8rem;
  }
}

@media screen and (max-width: 475px) {
  .footerMainContainer {
    padding: 2rem 2rem 2rem 4rem;
  }
}

@media screen and (max-width: 840px) {
  .footerMainContainer {
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}