.ourClientsMainContainer {
  display: flex;
  justify-content: center;
}

.ourClientsSubContainer {
  display: flex;
  flex-direction: row;
  margin: .5rem;
  padding: .5rem;
  max-width: 1000px
}

.ourClientsInformation {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.ourClientsImage img {
  width: 450px;
}

.ourClientsInfoTitle {
  font-size: 1.5rem;
}

.ourClientsInfoRegText {
  font-size: 1.15rem;
}

@media screen and (max-width: 840px) {
  .ourClientsSubContainer {
    flex-direction: column;
  }
}

@media screen and (max-width: 550px) {
  .ourClientsImage img {
    width: 325px;
  }
}